.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #6cbff0;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding-top: 2px;
  padding-bottom: 2px;
}

.body {
  background-color: #25272c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-footer {
  background-color: #6cbff0;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding-top: 2px;
  padding-bottom: 2px;
}

.App-link {
  color: #61dafb;
}

#frame {
  -webkit-transform:scale(0.7);
  -moz-transform:scale(0.7);
  -o-transform:scale(0.7);
  transform: scale(0.7) translate(-154px, -23%);
  
}

.container {
  display:flex;
  flex-direction: row;
}

.title {
  padding:30px;
  padding-top: 0px;
  padding-bottom: 0px;
  font-size: calc(20px + 2vmin);
  font-weight: bold;
  text-shadow: -1px 1px 10px black;
  transform: translate(0px, -3%);
}

.box{
  border: 3px solid white;
  width: 486px; height: 654px;
  overflow: hidden;
  margin: 50px;
}

.strip{
  background-color: #bfdff1;
  min-height: 1vh;
}